@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;padding:0;margin:0}body{text-align:center;font-family:Barlow Semi Condensed, sans-serif}label{margin-top:1rem}.flex{display:flex;align-items:center}.select{min-width:150px;margin-right:1rem}.selectCols{margin-top:0.5rem}.selectCols:nth-of-type(1){margin-top:0rem}.button{width:2rem;cursor:pointer;color:grey}.helpButton{position:relative;cursor:pointer;color:grey;width:1.2rem}.helpButton:hover{color:darker(grey, 2)}.tooltip .tooltip-text{visibility:hidden;width:12rem;padding:0.8rem;background-color:rgba(0,0,0,0.8);color:white;text-align:center;border-radius:10px;position:absolute;z-index:1}.tooltip:hover .tooltip-text{visibility:visible}.remove:hover{color:red}.add:hover{color:green}.title{background:#1f2a44;color:white;padding:1rem;font-style:normal}.form{padding:1rem;font-size:large;text-align:left}.form .checkbox{margin-right:0.5rem}.warning{font-style:italic;font-size:smaller;color:#555;width:24rem;margin:1rem 0rem}.file{position:relative;display:inline-block;cursor:pointer;height:2.5rem;text-align:left}.file input{min-width:20rem;margin:0;opacity:0}.file-custom{position:absolute;top:0;right:0;left:0;height:2.5rem;padding:0.5rem 1rem;line-height:1.5;color:#555;background-color:#fff;border:0.075rem solid #ddd;border-radius:0.25rem;box-shadow:inset 0 0.2rem 0.4rem rgba(0,0,0,0.05);-webkit-user-select:none;-ms-user-select:none;user-select:none}.file-custom .filename{display:inline-block;max-width:12rem;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.file-custom .browse{position:absolute;top:-0.075rem;right:-0.075rem;bottom:-0.075rem;display:block;content:'Browse';height:2.5rem;padding:0.5rem 1rem;line-height:1.5;color:#555;background:#eee;border:0.075rem solid #ddd;border-radius:0 0.25rem 0.25rem 0}.file-custom .browse:hover{background-color:#e4e4e4}.columns{margin-top:1rem}

